Drupal.behaviors.cardImageModal = {
  attach(context) {
    const card = context.querySelectorAll('.card-image-modal');
    card.forEach((item) => {
      item.addEventListener('click', () => {
        const cardModal = item.getAttribute('data-modal');
        const cardItem = context.getElementById(cardModal);
        cardItem.classList.toggle('show');
        const modalHidden = cardItem.querySelector('.close-modal');
        modalHidden?.addEventListener('click', () => {
          cardItem?.classList.remove('show');
        });
      });
    });
  },
};
